.app-wrapper {
  display: grid;
  grid-template-areas:
  'h h'
  'n c'
  'f f';
  grid-template-rows: 60px 1fr 60px;
  grid-template-columns: 2fr 10fr;

  position: relative;
  min-height: 100vh;
  width: 1200px;
  margin: 0 auto;
}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(199, 228, 247);
  padding-bottom: 60px;
}
